* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  color: white;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 2px;
  overflow-x: hidden;
}

body {
  background-color: #232323;
}



/* NAV BAR START */
.nav__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 8vh;
  background-color: #121212;
  border-bottom: 0.5px solid #343434;
}

.logo {
  margin-left: 2%;
  letter-spacing: 5px;
  font-size: 24px;
  border: 2px solid white;
  border-radius: 10%;
  padding: 3px;
}

.logo:hover {
  transition: 0.3s ease;
  color: #343434;
  border-color: #343434;
}

.navbar__list {
  display: flex;
  list-style: none;
  justify-content: space-around;
  width: 40%;
}

.navbar__list li:hover {
  transition: 0.3s ease;
  color: #343434;
  border-color: #343434;
}

.burger {
  display: none;
}

.burger div {
  width: 25px;
  height: 3px;
  margin: 5px;
  background-color: white;
}


@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden;
  }

  .navbar__list {
    width: 30%;
    position: absolute;
    right: 0px;
    height: 92vh;
    top: 8vh;
    background-color: #121212;
    display: flex;
    flex-direction: column;
    text-align: center;
    border-left: 1px solid #343434;
    align-items: center;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
  }

  .navbar__list li {
    opacity: 0;
  }

  .burger {
    cursor: pointer;
    margin-right: 5%;
    display: block;
  }

  .burger div {
    transition: all 0.3s ease;
  }

  .resume__container h2 {
    color: black;
    text-align: center;
  }

}

.nav__active {
  transform: translateX(0%);
}

.navbar__li__active li {
  opacity: 1;
  color: white;
    -webkit-transition: opacity 2s ease-in;
    -moz-transition: opacity 2s ease-in;
    -o-transition: opacity 2s ease-in;
    -ms-transition: opacity 2s ease-in;
    transition: opacity 2s ease-in;
}

.line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}

.line2 {
  opacity: 0;
}

.line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

/* NAV BAR END */

/* RESUME START */
.fa:hover {
  transition: 0.3s ease;
  color: pink;
}

.resume__container {
  background-color: white;
  width: 100vw;
  height: 100%;
  color: black;
}

.resume__name {
  font-size: 54px;
  text-align: center;
  padding: 20px;
  color: black;
}

.resume__container h5 {
  color: black;
  text-align: center;
  margin-bottom: 10px;
}

.resume__container h2, h3, h4, p, i {
  color: black;
  margin-top: 2%;
  margin-left: 10%;
  margin-right: 10%;
}

.resume__container h2 {
  background-color: black;
  padding: 0.5%;
  color: white;
}

.resume__container p {
  margin-top: 1%;
  line-height: 6vh;
}

.resume__container a {
  color: black;
  text-decoration: none;
  font-size: 14px;
}

.project__caption {
  font-style: italic;
  font-size: 20px;
}

.resume__container i {
  margin-top: 2%;
  font-size: 18px;
}

.project__container {
  padding-bottom: 2%;
}

.project__container h3 {
  font-size: 24px;
}

.project__container p {
  font-size: 16px;
}

.project__container a {
  font-size: 24px;
}

.work__container {
  display: flex;
  justify-content: space-between;
}

.work__container h2 {
  background-color: white;
  color: black;
}

.work__container h4 {
  margin-left: 11%;
  margin-right: 11%;
  margin-top: 5px;
  font-style: italic;
}

/* RESUME END */

/* START OF CONTACT */
.contact__container {
  position: fixed;
  font-size: 36px;
  top: 40%;
  left: 30%;
  right: 30%;
  line-height: 2em;
  text-align: center;
}

.contact__container a {
  text-decoration: none;
}


/* END OF CONTACT */

/* START OF HOME PAGE */
.home__container {
  text-align: center;
  margin-top: 20%;
  font-size: 50px;
}

.home__container a {
  text-decoration: none;
}

/* END OF HOME PAGE */

/* START OF PORTFOLIO */
.portfolio__container {
  text-align: center;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.portfolio__container span {
  display: block;
}

.text1 {
  font-size: 108px;
  letter-spacing: 4px;
  font-weight: 700;
  margin-bottom: -5px;
  position: relative;
  background-color: #232323;
  animation: text 3s 1;
  padding: 0;
}

.text2 {
  font-size: 34px;
  letter-spacing: 10px;
  color: #0892d0;
}

@keyframes text {
  0%{
    color: #232323;
    margin-bottom: -80px;
  }
  30%{
    letter-spacing: 50px;
    margin-bottom: -50px;
    color: #0892d0;
  }
}
/* END OF PORTFOLIO */
